@charset "utf-8";

html {
  overflow-y: scroll;
  position: relative;
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 1.6em;
  width: 100%;
  height: auto;
  min-height: 100%;
  max-width: 1920px;
  overflow-x: hidden;
}

body {
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 1.6em;
  width: 100%;
  height: auto;
  min-height: 100%;
  max-width: 1920px;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  &::before, &::after {
    box-sizing: border-box;
  }
}

#hideAll {
  display: none;
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  background-color: white;
  z-index: 99;
  /* Higher than anything else in the document */
  background-image: url('../images/ajax-loader.gif');
  background-repeat: no-repeat;
  background-position: center center;
}

.loader {
  border: 12px solid #f3f3f3;
  /* Light grey */
  border-top: 12px solid #1b0a40;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 2s linear infinite;
  &.loading_ajax.global_ajax_loader {
    position: fixed;
    width: 32px;
    height: 32px;
    bottom: 6px;
    left: 6px;
    border: 4px solid #f3f3f3;
    /* Light grey */
    border-top: 4px solid #1b0a40;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


.bold {
  font-weight: bold;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

.uppercase {
  text-transform: uppercase;
}

.capitalise {
  text-transform: capitalize;
}

.hidden {
  display: none;
}

.nospace {
  margin: 0;
  padding: 0;
  list-style: none;
}

.inline * {
  display: inline-block;
  &:last-child {
    margin-right: 0;
  }
}

.pushright li {
  margin-right: 20px;
  &:last-child {
    margin-right: 0;
  }
}

.borderedbox {
  border: 1px solid;
}

.overlay {
  position: relative;
  &::after {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: "";
    z-index: -1;
  }
}

.bgded {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

.circle {
  border-radius: 50%;
  background-clip: padding-box;
}

.btn {
  display: inline-block;
  padding: 15px 25px 17px;
  text-transform: uppercase;
  border: 1px solid;
}

.center-mrg {
  margin-left: auto;
  margin-right: auto;
}

/* general classes */

.margin-vertical {
  margin: 20px 0;
  &.medium {
    margin: 40px 0;
  }
}

.padding-horizontal.medium {
  padding-left: 30px;
  padding-right: 30px;
}

.text-block {
  display: block;
}

.text-nowrap {
  white-space: nowrap;
}

.no-float {
  float: none;
}

div.flexbox {
  display: flex;
}

@media (max-width: 991px) {
  div.flexbox {
    display: block;
  }
}

.absolute-middle {
  /* Internet Explorer 10 */
  display: -ms-flexbox;
  -ms-flex-pack: center;
  -ms-flex-align: center;
  /* Firefox */
  display: -moz-box;
  -moz-box-pack: center;
  -moz-box-align: center;
  /* Safari, Opera, and Chrome */
  display: -webkit-box;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  /* W3C */
  display: box;
  box-pack: center;
  box-align: center;
}

.clear, .group {
  display: block;
}

.clear {
  &::before, &::after {
    display: table;
    content: "";
  }
}

.group {
  &::before, &::after {
    display: table;
    content: "";
  }
}

.clear, .group {
  clear: both;
  &::after {
    clear: both;
  }
}

a {
  outline: none;
  text-decoration: none;
  &:hover, &:active {
    outline: none;
    text-decoration: none;
  }
}

.fl_left, .imgl {
  float: left;
}

.fl_right, .imgr {
  float: right;
}

img {
  width: auto;
  max-width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  border: none;
  line-height: normal;
  vertical-align: middle;
}

.imgl {
  margin: 0 15px 10px 0;
  clear: left;
}

.imgr {
  margin: 0 0 10px 15px;
  clear: right;
}

/* FULL HEIGHT SECTIONS */

div.screen-height, section.screen-height {
  min-height: 100vh;
  height: calc(100%);
  height: 100%;
}

div {
  &.tri-screen-height {
    height: 75%;
  }
  &.duo-screen-height {
    height: 50%;
  }
  &.pri-screen-height {
    height: 25%;
  }
  &.flex-fit {
    display: flex;
    flex-flow: column;
  }
  &.darkened {
    background-color: #f1f1f1;
    border: 1px solid #ddd;
  }
}

/* Custom */

.overlay::after {
  color: inherit;
  background-color: rgba(0, 0, 0, 0.65);
}

.preload * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
}

