#backtotop {
  z-index: 2;
  display: inline-block;
  position: fixed;
  visibility: hidden;
  bottom: 20px;
  right: 20px;
  width: 36px;
  height: 36px;
  line-height: 36px;
  font-size: 16px;
  text-align: center;
  opacity: 0.2;
  text-decoration: none;
  outline: none;
  transition: all .2s ease-in-out 0s;
  color: #fff;
  background-color: #ff9c10;
  i {
    display: block;
    width: 100%;
    height: 100%;
    line-height: inherit;
  }
  &.visible {
    visibility: visible;
    opacity: 0.5;
    &:hover {
      visibility: visible;
      opacity: 1;
    }
  }
}

