/* FLIPPER ///////////////////////////////////////////////// */
/* entire container, keeps perspective */

.flip-container {
  perspective: 1000px;
  &.hover .flipper {
    transform: rotateX(180deg);
    transition-delay: 0s;
  }
  width: 100%;
  height: auto;
}

/* flip the pane when hovered */
/* .flip-container:hover .flipper, */

.flipper {
  .front, .back {
    width: 100%;
    height: auto;
  }
  transition: 0.6s;
  transition-delay: 0.6s;
  transform-style: preserve-3d;
  position: relative;
  height: 100%;
  .front, .back {
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }
  .front {
    z-index: 2;
    /* for firefox 31 */
    transform: rotateX(0deg);
  }
  .back {
    transform: rotateX(180deg);
  }
}

