/* ALIGNER -------------------------------------------------- */

div.aligner.all {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}

.aligner-item--fixed.all {
  -webkit-box-flex: 0;
  -webkit-flex: none;
  flex: none;
}

.aligner-item.all {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
}

.flex-center.all {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 200px;
  resize: vertical;
  &.vertical-center {
    align-items: center;
    justify-content: flex-start;
  }
  &.absolute-center {
    align-items: center;
    justify-content: center;
  }
  &.flex-row {
    flex-direction: row;
  }
  p {
    margin: 0;
    padding: 20px;
  }
}

/* /ALIGNER -------------------------------------------------- */

